.haiang_box {
    position: relative;
    width: 50vw;
    height: 50vh;
    padding: 10px;
    margin: auto;
    color: black;
    border-radius: 20px;
    background-color: #ffffffec;
    border: 8px solid transparent;
    border-image: url("../../img/sprite/conversation_box.png") 3 stretch;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
    animation: popup 0.5s ease-in-out;
    box-shadow: 0 0 12px rgb(0 0 0 / 63%);
  }

  @keyframes popup {
    from {
      transform: translate(0, 30px);
    }
    to {
      transform: translate(0, 0px);
    }
  }
  
  .haiang_box p {
    text-align: left;
    margin-top: 0px;
    margin-left: 12px;
    margin-right: 40px;
    line-height: 2;
  }

  .haiang_box_btn_close {
    position: absolute;
    background-image: url("../../img/sprite/conversation_box_close_btn.png");
    background-size: cover;
    background-color: transparent;
    border-width: 0;
    width: 48px;
    height: 54px;
    right: -18px;
    top: -18px;
    cursor: pointer;
  }


  