#canbeerfrog_unoopen_box {
  background-image: url(../../img/sprite/walkingcat_box.png);
  height: 100%;
}

#canbeerfrog_oopen_box {
  background-image: url(../../img/sprite/walkingcat_openedbox.png);
  height: 100%;    
}

#purple_note {
  color: #887aa2;
}
#red_note {
  color: #ff8080;
}
#green_note {
  color: #70a870;
}
#white_note {
  color: #ffffff;
}

#canbeerfrog_answer {
  display: block;
  position: absolute;
  background: rgba(255, 255, 255, 0.37);
  padding: 3px;
  width: 130px;
  height: 40px;
  left: 50px;
  top: 220px;
  font-size: 20pt;
  letter-spacing: 5px;
  border-radius: 6px;
}

#canbeerfrog_answer i {
  display: inline-block;
}

#tape_btn_content {
  position: absolute;
  width: 140px;
  right: 45px;
  bottom: 70px;
  padding: 0;
}

#tape_btn_content li {
  margin: 3px;
  display: inline-block;
}

#canbeerfrog_open_btn {
  position: absolute;
  left: 80px;
  bottom: 110px;
  font-size: 16px;
}