#boatwork_unopen_box {
    background-image: url(../../img/sprite/walkingcat_box.png);
    height: 100%;
}

#key_hole {
    position: absolute;
    border-radius: 20px;
    height: 100px;
    width: 30px;
    right: 100px;
    bottom: 100px;
}

#boatwork_unopen_box #key_hole {
    background: black;
}

#boatwork_open_box #key_hole {
    background: rgb(255, 196, 0);
}

#boatwork_open_box {
    background-image: url(../../img/sprite/walkingcat_openedbox.png);
    height: 100%;
}