.frame {
  display: inline-block;
  overflow: hidden;
  margin: 3pt;
  border-radius: 3pt;
  border: 3px solid white;
  transition: ease-out 0.2s;
}

.frame.sparkle {
  box-shadow: 0 0 20px 4px #fffefc;
  transition: ease-out 0.5s;
}

.frame.sparkle:hover {
  filter: drop-shadow();
  box-shadow: 0 0 20px 4px #fffefc88;
  transition: ease 0.5s;
}

.haiang_work {
  position: relative;
  width: 450px;
  height: 450px;
  cursor: pointer;
}

.introduction_box {
  background-color: rgb(218, 218, 218);
  color: rgb(68, 68, 68);
  font-size: 12pt;
  width: 145px;
  padding: 6px;
  margin: 9px 0px 0 12px;
  letter-spacing: 0.15em;
  text-align: left;
  font-weight: bolder;
  box-shadow: 0 0 3px 0px rgb(150, 150, 150);
}

.introduction_box p {
  color: rgba(255, 255, 255, 0.747);
  padding: 0 0 0px 15px;
  margin: 12px 0;
  letter-spacing: 0em;
  line-height: 0.5em;
  text-align: right;
  font-weight: normal;
}

.introduction_box p.intro_box_author {
  padding: 0 0 0px 0px;
  margin: 0 0;
  font-size: xx-small;
  letter-spacing: 0.3em;
}

.work_mechanism {
  height: 50px;
  width: 50px;
  margin: auto;
  margin-top: 60px;
  margin-bottom: -5px;
  cursor: pointer;
  z-index: 2;
  transition: ease-out 0.2s;
  background-image: url(../../img/sprite/table.png);
  background-size: cover;
}

.work_mechanism.sparkle {
  filter: drop-shadow(0 0px 20px #aefcff);
  transition: ease-out 0.5s;
}

.work_mechanism.sparkle:hover {
  filter: drop-shadow(0 0px 10px #fffefc88);
  transition: ease 0.5s;
}

.work_reward_btn {
  position: absolute;
  left: 52px;
  top: 250px;
  font-size: 12pt;
  padding: 10px 15px 28px 15px;
  border-width: 0;
  border-radius: 3px;
  color: white;
  background: rgba(240, 134, 108, 0);
  transition: ease-in 0.15s;
  box-shadow: 0 0 9px rgba(0, 0, 0, 0);
  letter-spacing: 0.2em;
}

.work_reward_btn:hover {
  background: rgb(240, 134, 108);
  top: 245px;
  transition: ease-in 0.15s;
  cursor: pointer;
  box-shadow: 0 0 50px black;
  animation: blink 2s ease-out infinite;
}

@keyframes blink {
  0% {
    top: 245px;
  }
  50% {
    top: 230px;
  }
  100% {
    top: 245px;
  }
}
