#game {
  background-color: #fbe461;
  overflow: hidden;
}

#gallery {
  display: flex;
  padding: 5vh 12pt 0 12pt;
  background-color: #acadb2;
  box-shadow: inset -2px -3px 20px 0px #00000021;
}

.haiang_workplace {
  flex: 1;
  flex-grow: 0;
  flex-shrink: 1;
}

div,
img {
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
}

.gallery_wall {
  position: relative;
  flex: 1;
  flex-grow: 2;
  flex-shrink: 2;
  padding: 3pt;
}

#gallery_previous_room,
#gallery_next_room {
  position: absolute;
  background: rgb(77, 77, 77);
  color: white;
  min-width: 80px;
  min-height: 140px;
  bottom: 0px;
  cursor: pointer;
  border: 3px solid #acadb2;
  box-shadow: inset 12px 2px 20px 6px #00000087;
  transition: ease 2s;
  z-index: 1;
}

#gallery_previous_room:hover,
#gallery_next_room:hover {
  box-shadow: inset -20px 13px 20px 6px #00000087;
  transition: ease-in 0.5s;
}

#gallery_previous_room {
  right: 50px;
  background-image: url(../img/sprite/door_left.png);
  background-repeat: no-repeat;
  background-size: 80px 140px;
}

#gallery_next_room {
  left: 50px;
  background-image: url(../img/sprite/door_right.png);
  background-repeat: no-repeat;
  background-size: 80px 140px;
}

#chat_box {
  background-color: rgb(161, 127, 255);
}

.grid_unit {
  width: 3vw;
  height: 3vw;
}

.sb-soundplayer-play-btn {
  width: 2.5em;
}

li {
  display: inline;
}

mark {
  background-color: #ffffff00;
  color: #007fd4;
}

#floor {
  background-color: #dfdfdf;
  height: 10vh;
  box-shadow: inset -2px -3px 20px 0px #00000021;
}

.left_decorate_work,
.right_decorate_work {
  background-color: rgba(255, 255, 255, 0.815);
  width: 150px;
  height: 150px;
  margin: auto;
  margin-top: 20vh;
  box-shadow: 0px 9px 14px -5px #000000a1;
}

.left_decorate_work img {
  width: 100%;
  border-radius: 3px;
  border: 3px solid white;
}

.intro_board {
  background-color: rgb(218, 218, 218);
  font-size: xx-small;
  color: white;
  width: 50px;
  padding: 6px;
  margin: 20px auto;
  letter-spacing: 0em;
  text-align: right;
  line-height: 0.7em;
  box-shadow: 0 0 3px 0px rgb(150, 150, 150);
}

#ticket {
  position: fixed;
  background-color: white;
  background-image: url(../img/sprite/ticket.png);
  background-size: cover;
  width: 200px;
  height: 100px;
  left: 0;
  z-index: 10000;
  box-shadow: 0 0 20px rgb(0 0 0 / 39%);
  transition: 0.2s ease-in;
}

#ticket:hover {
  left: -9px;
  transition: 0.2s ease-in-out;
  cursor: pointer;
}

#ticket ul {
  margin: auto;
  padding: 6px 0 0 50px;
}

.ticket_grid {
  position: relative;
  display: inline-block;
  margin: 3px;
  width: 35px;
  height: 35px;
  background-color: #6666685e;
  overflow: hidden;
  border-radius: 50%;
}

.ticket_grid img {
  border-radius: 50%;
  padding: 3px;
  animation: stamping ease-out 1s;
}

@keyframes stamping {
  0% {
    background-color: #485bdb;
  }
  100% {
    background-color: #485cdb00;
  }
}


.ticket_reward_blink#ticket {
  box-shadow: 0 0 20px 11px #fff6e2d8;
  animation: reward_blink ease-in-out 1s infinite;
}

@keyframes reward_blink {
  0% {
    box-shadow: 0 0 20px 8px #fff6e2d8;
  }
  50% {
    box-shadow: 0 0 20px 11px #fff6e2d8;
  }
  100% {
    box-shadow: 0 0 20px 8px #fff6e2d8;
  }
}

.stamp {
  background-color: #485bdb;
}

#reward_button {
  margin-top: 15px;
  font-size: large;
}

#curtain {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10000;
  animation: curtain_ease_out 1s;
  transform: scale(0);
  background: black;
  opacity: 0;
}

@keyframes curtain_ease_out {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  50% {
    opacity: 1;
  }

  99% {
    opacity: 0;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0);
  }
}

#game_intro {
  display: flex;
  flex-direction: column;
  height: 100vh;
  color: white;
  letter-spacing: 0.3em;
  justify-content: center;
  align-items: center;
  font-size: large;
  background: rgba(0, 0, 0, 0.568);
  animation: intro_slide_in 2s ease-out;
}

@keyframes intro_slide_in {
  0% {
    height: 120vh;
    opacity: 0;
  }
  30% {
    height: 120vh;
    opacity: 0;
  }
  100% {
    height: 100vh;
    opacity: 1;
  }
}

#game_intro * {
  animation: intro_fade_in 2s ease-in;
}

@keyframes intro_fade_in {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

#game_intro mark {
  font-weight: bolder;
  color: rgb(111, 174, 255);
}

#game_intro button {
  font-weight: bolder;
  background-color: rgb(91, 142, 209);
  color: white;
  font-size: large;
  border-width: 0;
  padding: 12px;
  border-radius: 3px;
  margin-top: 12px;
  transition: ease-out 0.5s;
}

#game_intro button:hover {
  background-color: rgb(73, 111, 161);
  transition: ease-in 0.15s;
  cursor: pointer;
}

#game_message {
  display: inline-block;
  position: fixed;
  top: 15vh;
  left: 0;
  width: 100vw;
  height: 10vh;
  opacity: 0;
  z-index: 1000;
  animation: fade_in_and_out 5s;
}

#game_message h3 {
  display: inline-block;
  padding: 10px;
  background: white;
}

@keyframes fade_in_and_out {
  0% {
    top: 10vh;
    opacity: 0;
  }

  20% {
    top: 15vh;
    opacity: 1;
  }

  100% {
    top: 10vh;
    opacity: 0;
  }
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button {  
   opacity: 1;
}
