#bears_photos {
  width: 150px;
}

#bears_photos_content {
  position: absolute;
  right: 40px;
  top: 40px;
}

#bears_unopen_box {
  background-image: url(../../img/sprite/walkingcat_box.png);
  height: 100%;
}

#bears_open_box {
  background-image: url(../../img/sprite/walkingcat_openedbox.png);
  height: 100%;    
}

#bears_timer {
  display: inline-block;
  position: absolute;
  width: 150px;
  right: 40px;
  bottom: 110px;
}

#bears_timer input {
  width: 35px;
  height: 30px;
  font-size: 16px;
  
}

#bears_timer button {
  margin-top: 10px;
  font-size: medium;
}