.conversarion_content {
  letter-spacing: 0.1em;
  font-size: larger;
}

#talker_name {
  vertical-align: baseline;
  display: inline-block;
  position: relative;
  width: 76px;
  height: 33px;
  padding: 0px;
  color: black;
  font-weight: bolder;
  font-size: x-large;
  margin-right: 12px;
  background-image: url("../../img/sprite/conversation_box_name.png");
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}

.btn_next {
  position: absolute;
  right: 12px;
  bottom: 12px;
  width: 40px;
  height: 26px;
  border: 0px;
  background-color: transparent;
  background-image: url("../../img/sprite/conversation_box_next_btn.png");
  background-size: cover;
}

#dialog_modal {
  bottom: 10vh;
}

#dialog_box {
  width: 50vw;
  max-width: 600px;
  max-height: 150px;
}
