.partitioned {
  padding-left: 10px;
  letter-spacing: 35px;
  border: 0;
  background-image: linear-gradient(
    to left,
    rgb(255, 255, 255) 70%,
    rgba(255, 255, 255, 0) 0%
  );
  background-position: bottom;
  background-size: 50px 50px;
  background-repeat: repeat-x;
  background-position-x: 35px;
  width: 125px;
  font-family: monospace;
}

#walking_cat_answer_input {
  position:absolute;
  bottom: 130px;
  left: 50px;
  height: 35px;
  font-size: 20pt;
}

#walkingcat_unopen_box {
  background-image: url(../../img/sprite/walkingcat_box.png);
  height: 100%;
}

#walkingcat_open_box {
  background-image: url(../../img/sprite/walkingcat_openedbox.png);
  height: 100%;    
}

#walking_cats_mail_open,
#walking_cats_mail_unopen {
  width: 180px;
  top: 10px;
}

#walking_cat_mail {
  position: absolute;
  right: 10px;
  top: 40px;
}

#walkingcat_keyboard {
  position: absolute;
  width: 100px;
  right: 65px;
  bottom: 90px;
}

.mechanism_card {
  position: absolute;
  top: 100px;
  left: 50px;
  font-size: 10pt;
}