#windowandcat_unopen_box {
  background-image: url(../../img/sprite/walkingcat_box.png);
  height: 100%;
}

#windowandcat_open_box {
  background-image: url(../../img/sprite/walkingcat_openedbox.png);
  height: 100%;
}

#hertz {
  position: absolute;
  bottom: 120px;
  left: 45px;
  width: 130px;
  letter-spacing: 0.2em;
  background: rgba(255, 255, 255, 0.514);
  padding: 9px;
  border-radius: 6px;
}

#windowandcat_hertz_slide {
  position: absolute;
  left: 295px;
  top: 200px;
}

input[type="range"][orient="vertical"]#windowandcat_hertz_slide {
  writing-mode: bt-lr; /* IE */
  -webkit-appearance: slider-vertical; /* WebKit */
  width: 8px;
  height: 140px;
  padding: 0 5px;
}

#windowandcat_open_box_button {
  position: absolute;
  font-size: 12pt;
  left: 78px;
  bottom: 95px;
}

#whale_sound_radio {
  position: absolute;
  right: 50px;
  top: 35px;
  background-image: url(../../img/sprite/sound_plate.png);
  background-size: cover;
  width: 120px;
  height: 120px;
}

#whale_sound_radio .sb-soundplayer-icon {
  color: #e6e6e6;
  width: 18px;
}

#whale_sound_radio .sb-soundplayer-btn {
  background: #8e8e8e;
  padding: 9px;
  border-radius: 6px;
  width: 60px;
  height: 40px;
  margin-top: 9px;
  border-width: 3px;
}

#whale_sound_radio .sb-soundplayer-timer {
  color: #a3a3a3;
  background-color: rgba(255, 255, 255, 0.103);
  padding: 3px 0 0 3px;
  font-size: 6pt; 
  width: 43px;
  height: 20px;
  overflow: hidden;
  margin: 23px auto;
  letter-spacing: .2em;
  border-radius: 3px;
}